import React, { useState } from 'react';

import  { useEffect,useRef } from 'react';
import "./PlayGround.css"
// Blockly
import Blockly from 'blockly/core';
import {javascriptGenerator} from 'blockly/javascript';
import 'blockly/blocks';
// 
import {generatefinalCodes} from '../utils/codetool'
import {saveTxt,uploadTxt} from '../utils/IOdata';
import version from "../config/version"
// 
import locale from 'blockly/msg/zh-hans';

Blockly.setLocale(locale);

// 
// calling IPC exposed from preload script
if(window.electron&&window.electron.ipcRenderer){
    window.isinWebpageMode=false
    window.electron.ipcRenderer.on('ipc-example', (arg) => {
        // eslint-disable-next-line no-console
        // console.log(arg);
        return
    });
}else{
    console.log("thisway")
    window.isinWebpageMode=true
}


// 
function antiShake(fun, delay) {
    window.genrun = null;
    return function (e) {
        clearTimeout(window.genrun);
        window.genrun = setTimeout(() => {
            fun.apply(this, arguments);
        }, delay)
    };
}



function PlayGround(props){
    const blocklyDiv = useRef();
    const toolbox = useRef();
    let primaryWorkspace = useRef();
    
    // window.wfilepath

    // esultcode
    let [resultcode,setResultcode]=useState("")
    // 
    let [showres,setShowres]=useState(true)

    // Playground
    useEffect(() => {
        const { initialXml, children, ...rest } = props;
            primaryWorkspace.current = Blockly.inject(
                blocklyDiv.current,
                {
                    toolbox: toolbox.current,
                    ...rest
                },
            );

            if (initialXml) {
                Blockly.Xml.domToWorkspace(Blockly.Xml.textToDom(initialXml), primaryWorkspace.current);
            }
            // 
            primaryWorkspace.current.addChangeListener(
                antiShake(()=>{
                    antiSaveFile()
                },500)
            );

    }, [primaryWorkspace, toolbox, blocklyDiv, props]);
    // 
    const loadProject=(e)=>{
        if(e.target && e.target.files){
            const file=e.target.files[0];
            // 
            const filenamesegs=file.name.split(".")
            if(filenamesegs[filenamesegs.length-1]==="bablockly"){
                uploadTxt(file,function(str){
                    let workspaceObj=JSON.parse(str)
                    try{
                        Blockly.serialization.workspaces.load(workspaceObj, primaryWorkspace.current);
                    }catch(error){
                        setResultcode("blockly")
                    }
                })
            }else{
                setResultcode("bablockly")
            }
        }
        
    }
    // 
    const saveProject=()=>{
        saveTxt(`ArisStudio_blockly_${version}.bablockly`,JSON.stringify(Blockly.serialization.workspaces.save(primaryWorkspace.current)))
    }
    /**
     * 
     *  */ 
    const generateCode = () => {
        // 0
        // playgroundwindownuminbigfunc0utils/timestamp0if1
        // ififid
        window.numinbigfunc=0;
        // playground
        let areacode = javascriptGenerator.workspaceToCode(
          primaryWorkspace.current
        );
        // 
        const playcode=generatefinalCodes(areacode)
        window.playcode=playcode
        // 
        // windowmakecodetxtwindow.txtcode
        try {
            window.eval(playcode)
            setResultcode(window.txtcode)
        } catch (error) {
            setResultcode(`${error.message}`)
        }
    }
    // 
    const selectFilepath=(e)=>{
        if(e.target&&e.target.files){
            window.wfilepath=e.target.files[0].path // 
            setResultcode(":"+window.wfilepath)
        }else{
            setResultcode("")
        }
    }

    // electron playground 
    const antiSaveFile=()=>{
        generateCode();
        if(window.wfilepath){
            if(window.wfilepath.length>0){
                if(window.electron&&window.electron.ipcRenderer){
                    window.electron.ipcRenderer.sendMessage('ipc-example', [window.wfilepath, window.txtcode]);
                }
            }
        }

    }

    // web 
    const downloadCode=()=>{
        generateCode()
        saveTxt(`demoASblockly.txt`,window.txtcode)
    }

    return (
    <>
        <span id="toolsbox">
            :{version}
                <div>
                    <button className="loadprojectbutton"><input type="file" name="file" accept='*' className="projectfile" onChange={loadProject}></input>blockly</button>
                    <button onClick={saveProject}>blockly</button>
                </div>
                <div>
                    {window.isinWebpageMode?<></>:<><button className="loadprojectbutton" style={{width:"120px"}}><input type="file" name="file" accept='text/plain' className="projectfile" onChange={selectFilepath}></input>{window.wfilepath?"":""}</button></>}
                    
                    <button onClick={downloadCode}></button>
                </div>
                <div>
                    <button onClick={()=>setShowres(!showres)}>{showres?"":""}</button>
                </div>
                
                
                

        </span>
        <span ref={blocklyDiv} id="blocklyDiv" />
        <div style={{ display: 'none' }} ref={toolbox}>
            {props.children}
        </div>
        {/*   */}
        {showres?<textarea spellCheck={false} id="rescodebox" value={resultcode}></textarea>:<></>}


    </>);
}

export default PlayGround;

// 
const Block = (p) => {
    const { children, ...props } = p;
    props.is = "blockly";
    return React.createElement("block", props, children);
};

const Category = (p) => {
    const { children, ...props } = p;
    props.is = "blockly";
    return React.createElement("category", props, children);
};

const Value = (p) => {
    const { children, ...props } = p;
    props.is = "blockly";
    return React.createElement("value", props, children);
};

const Field = (p) => {
    const { children, ...props } = p;
    props.is = "blockly";
    return React.createElement("field", props, children);
};

const Shadow = (p) => {
    const { children, ...props } = p;
    props.is = "blockly";
    return React.createElement("shadow", props, children);
};

export { Block, Category, Value, Field, Shadow }